import React, { useEffect } from "react";
import {
  Chart,
  ArcElement,
  LineElement,
  BarElement,
  PointElement,
  BarController,
  BubbleController,
  DoughnutController,
  LineController,
  PieController,
  PolarAreaController,
  RadarController,
  ScatterController,
  CategoryScale,
  LinearScale,
  LogarithmicScale,
  RadialLinearScale,
  TimeScale,
  TimeSeriesScale,
  Decimation,
  Filler,
  Legend,
  Title,
  Tooltip,
} from "chart.js";

Chart.register(
  ArcElement,
  LineElement,
  BarElement,
  PointElement,
  BarController,
  BubbleController,
  DoughnutController,
  LineController,
  PieController,
  PolarAreaController,
  RadarController,
  ScatterController,
  CategoryScale,
  LinearScale,
  LogarithmicScale,
  RadialLinearScale,
  TimeScale,
  TimeSeriesScale,
  Decimation,
  Filler,
  Legend,
  Title,
  Tooltip
);

const cntTime_2last = window.cntTime_2last;
const cntTime_last = window.cntTime_last;
const cntTime_this = window.cntTime_this;

const canvas = (
  <canvas
    className="totalChargeTime-canvas"
    style={{ width: "95%", height: "95%" }}
  ></canvas>
);

function TotalChargeTime() {
  useEffect(() => {
    let today = new Date();

    let month = today.getMonth() + 1;
    let date = today.getDate();

    let hours = today.getHours();
    let minutes = today.getMinutes();

    if (minutes / 10 < 1) {
      minutes = `0${minutes}`;
    }

    let dayBeforeYesterday = `${month}/${date - 2}`;
    let yesterday = `${month}/${date - 1}`;
    let currentHour = `${hours}:${minutes}`;

    const labels = [dayBeforeYesterday, yesterday, currentHour];

    const ctx = document
      .querySelector(".totalChargeTime-canvas")
      .getContext("2d");

    const chart = new Chart(ctx, {
      type: "bar",
      data: {
        labels: labels,
        datasets: [
          {
            data: [cntTime_2last, cntTime_last, cntTime_this],
            backgroundColor: [
              "rgba(107, 107, 107, 100)",
              "rgba(107, 107, 107, 100)",
              "rgba(255, 206, 0, 100)",
            ],
            barThickness: 14,
          },
        ],
      },
      options: {
        layout: {
          padding: {
            right: 10,
          },
        },
        plugins: {
          legend: {
            display: false,
          },
        },
        responsive: false,
        scales: {
          x: {
            title: {
              display: true,
              text: "날짜",
              font: {
                family: "Noto Sans KR",
                weight: 400,
                size: 13,
              },
              color: "rgb(107, 107, 107)",
            },
          },
          y: {
            title: {
              display: true,
              text: "분",
              font: {
                family: "Noto Sans KR",
                weight: 400,
                size: 13,
              },
              color: "rgb(107, 107, 107)",
            },
            beginAtZero: true,
          },
        },
      },
    });
  });

  return <div className="totalChargeTime-container">{canvas}</div>;
}

export default TotalChargeTime;
