const stationCode = window.stationCode;
const stationNumber = window.stationNumber;
const portNumber = window.portNumber;
const userName = window.userName;
const startTime = window.startTime;
const endTime = window.endTime;
const totalUsage = window.totalUsage;

export const data = [];

for(let i = 0; i < stationCode.length; i++){
  const row_data = {
    stationNumber: stationCode[i],
    portNumber: `${stationNumber[i]} / ${portNumber[i]}`,
    userName: userName[i],
    startTime: startTime[i],
    endTime: endTime[i],
    chargeTime: "10h 35m",
    electricEnergy: totalUsage[i]
  }

  data.push(row_data);
}

