import React from "react";
import icon from "../images/calendar_icon.png";

class CalendarIcon extends React.Component {
  render() {
    return (
      <img
        className="calendarIcon"
        style={{ width: "24px", height: "24px" }}
        src={icon}
        alt="calendar icon"
      ></img>
    );
  }
}

export default CalendarIcon;
